html, body, #root, #root>div {
  height: 100vh;
  width: 100vw;
}

#root {
  background-color: black;
}

.react-p5 {
  display: flex;
  justify-content: center;
}

.input-container {
  justify-content: center;
  display: flex;
  margin-top: 1rem;
  color: white;
  padding-bottom: 1rem;
}

@media only screen and (max-width: 480px) {
  .input-container {
    flex-direction: column;
  }
}
